<template>
    <ul class="menu">
        <li v-for="item in props.items"
            :key="item.code"
            :class="{
                active: item.code === props.code,
                total: !!item.total
            }"
            @click="emit('change', item.code)"
        >
            <span>
                {{ t(item.name) }}
            </span>

            <Total v-if="item.total" :total="item.total" />
        </li>
    </ul>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import Total from 'ðŸ”—/components/ui/Total.vue';

    const { t } = useI18n();
    const emit = defineEmits(['change']);

    const props = defineProps({
        items: { type: Array, default: () => ([]) },
        code: { type: [String, null], default: null }
    });
</script>

<style lang="scss" scoped>
    .menu {
        display: flex;
        flex-direction: row;
        gap: 8px;
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            font: var(--font-h7);
            color: var(--dark-80);
            cursor: pointer;
            padding: 8px 8px;
            transition: all .4s;
            position: relative;
            transition: all .4s;
            display: flex;
            flex-direction: row;
            gap: 4px;
            align-items: center;

            &:after {
                content: '';
                height: 2px;
                width: 16px;
                background: transparent;
                transition: background .4s;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
            }

            &.total {
                &:after {
                    transform: translateX(calc(-50% - 10px));
                }
            }

            &:first-child {
                padding-left: 0;
                &:after {
                    left: calc(50% - 4px);
                }
            }

            &:hover {
                color: var(--dark-80);
                &:after {
                    background-color: var(--dark-20);
                }
            }

            &.active {
                position: relative;
                color: var(--dark-500);
                &:after {
                    background: var(--dark-500);
                }
                
            }
        }

        &.minimal {
            li {
                &:after {
                    display: none;
                }
            }
        }

        &.invert {
            li {
                font: var(--font-body-1);
                color: var(--white-80);
                &:hover {
                    color: var(--white-80);
                    &:after {
                        background-color: var(--white-20);
                    }
                }

                &.active {
                    position: relative;
                    color: var(--white-500);
                    &:after {
                        background: var(--white-500);
                    }
                    
                }
            }
        }
    }
</style>
